<template>
  <div class="stat">
    <div class="opt">
      <input type="month" v-model="date" @change="refreshChart" />
    </div>
    <div id="history" v-loading="loadings.historyStasis" class="chart-item"></div>
    <div id="view_bar" v-loading="loadings.viewStasis" class="chart-item"></div>
    <div id="pie" v-loading="loadings.blogStasis" class="chart-item"></div>
    <div id="bar" v-loading="loadings.viewStasis" class="chart-item"></div>
  </div>
</template>

<script>
import { blogStasis, ppv_history, ppv_domestic } from '@/apis/index.js';
import { dayjs } from 'element-plus';

import { lineGraph, pieGraph, horizontalBarGraph } from './chartGenerator';

export default {
  name: 'Stat',
  data() {
    return {
      loadings: {
        historyStasis: true,
        pv_historyStasis: true,
        ipStatis: true,
        blogStasis: true,
        distriStasis: true,
        viewStasis: true,
      },
      todayViews: [],
      date: null,
    };
  },
  beforeMount() {
    this.date = dayjs().format('YYYY-MM');
  },
  mounted() {
    this.refreshChart();
  },
  methods: {
    refreshChart() {
      let params = { date: this.date + '-01' };
      Promise.allSettled([ppv_history(params), blogStasis({ type: 'history', params })])
        .then(res => {
          let series = [];
          if (res[0].status === 'fulfilled') {
            series.push({
              name: '具名访问',
              type: 'line',
              data: res[0].value.data.status ? res[0].value.data.data.values : [],
            });
          }
          if (res[1].status === 'fulfilled') {
            series.push({
              name: '匿名名访问',
              type: 'line',
              data: res[1].value.data.status ? res[1].value.data.data.values : [],
            });
          }
          lineGraph({ title: '页面访问', series, labels: res[0].value.data.data.labels }, 'history', '');
        })
        .catch(err => {
          console.error(err);
        })
        .finally(() => {
          this.loadings.historyStasis = false;
          this.loadings.pv_historyStasis = false;
        });

      blogStasis({ type: 'category', params }).then(response => {
        pieGraph(response.data.data, 'pie');
        this.loadings.blogStasis = false;
      });

      ppv_domestic(params).then(res => {
        const labels = [];
        const values = [];
        if (res.data.status) {
          res.data.data.forEach(item => {
            labels.push(item.name);
            values.push(item.value);
          });
          horizontalBarGraph(
            { title: '访问统计', series: { name: '具名', type: 'bar', data: values }, labels },
            'view_bar'
          );
        }
      });

      blogStasis({ type: 'blogView', params }).then(res => {
        const { labels, values } = res.data.data;
        horizontalBarGraph(
          {
            title: '博文访问统计',
            series: [{ name: '博文访问量', type: 'bar', data: values }],
            labels,
          },
          'bar'
        );
        this.loadings.viewStasis = false;
      });
    },
  },
};
</script>

