.stat {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1vw;
  width: 100%;
  .opt {
    display: flex;
    width: 100%;
    input {
      flex: 1;
    }
  }
  .chart-item {
    width: 100%;
    height: 50vh;
    background-color: rgba(240, 240, 245, 0.683);
  }
}
</style>
