import * as echarts from 'echarts/core';
import { UniversalTransition, LabelLayout } from 'echarts/features';
import { BarChart, PieChart, LineChart } from 'echarts/charts';
import {
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  ToolboxComponent,
  GridComponent,
} from 'echarts/components';
import { CanvasRenderer } from 'echarts/renderers';

echarts.use([
  UniversalTransition,
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  PieChart,
  LineChart,
  BarChart,
  CanvasRenderer,
  LabelLayout,
  ToolboxComponent,
  GridComponent,
]);

export function lineGraph({ labels, series, title }, divTagId) {
  let chartObj = echarts.init(document.getElementById(divTagId));
  let option = {
    title: {
      text: title,
    },
    tooltip: {
      trigger: 'axis',
    },
    legend: {},
    toolbox: {
      show: true,
      feature: {
        dataZoom: {
          yAxisIndex: 'none',
        },
        dataView: { readOnly: false },
        magicType: { type: ['line', 'bar'] },
        restore: {},
        saveAsImage: {},
      },
    },
    xAxis: {
      type: 'category',
      boundaryGap: false,
      data: labels,
    },
    yAxis: {
      type: 'value',
    },
    series: series,
  };
  option && chartObj.setOption(option);
  window.addEventListener('resize', () => {
    setTimeout(() => {
      chartObj.resize();
    }, 1000);
  });
}

export function pieGraph(data, divTagId) {
  var chartObj = document.getElementById(divTagId);
  var chartObj = echarts.init(chartObj);
  var option = {
    title: {
      text: '博客分类',
      left: 'center',
    },
    tooltip: {
      trigger: 'item',
    },
    legend: {
      orient: 'vertical',
      left: 'left',
    },
    series: [
      {
        name: '类别',
        type: 'pie',
        radius: '50%',
        data: data,
        emphasis: {
          itemStyle: {
            shadowBlur: 10,
            shadowOffsetX: 0,
            shadowColor: 'rgba(0, 0, 0, 0.5)',
          },
        },
      },
    ],
  };

  option && chartObj.setOption(option);
}
export function horizontalBarGraph({ labels, series, title }, divTagId) {
  var chartDom = document.getElementById(divTagId);
  var chartObj = echarts.init(chartDom);
  var option;
  option = {
    title: {
      text: title,
    },
    tooltip: {
      trigger: 'axis',
      axisPointer: {
        type: 'shadow',
      },
    },
    legend: {},
    grid: {
      left: '3%',
      right: '4%',
      bottom: '3%',
      containLabel: true,
    },
    xAxis: {
      type: 'value',
      boundaryGap: [0, 0.01],
    },
    yAxis: {
      type: 'category',
      data: labels,
    },
    series: series,
  };

  option && chartObj.setOption(option);
}
